

import {Icon,Modal ,Input,message,Layout} from 'antd';
import React from 'react';
import CryptoJS from "crypto-js";
import Markdown from 'react-markdown';


class JsPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            key:'',
            disVisible: true,  //不显示
            window: false ,// 弹框
        }
        if(props.location.search){
            const key = props.location.search.substring(1);
            if(key && key =='show'){
                this.state.window = true ;
            }
        }
    }


    handleOk0 = (e) => {
        if(this.state.key){
            const word =  "U2FsdGVkX1/QLZQYig/1DpXDqTtk6i7JjnmkJcC+Is0=";
            try{
                let bytes  = CryptoJS.AES.decrypt(word,this.state.key);
                let right_pass = bytes.toString(CryptoJS.enc.Utf8);
                if(right_pass){
                    this.setState({
                        disVisible: false , //显示
                        window:false,
                    });
                }else {
                    this.showNothing();
                }
            }catch (e) {
                this.showNothing();
            }

        }else{
            this.showNothing();
        }
    }


    showNothing = () => {
        this.setState({
            disVisible: true,
            window:false,
        });
    }

    onChangeUserName = (e) => {
        this.setState({ key: e.target.value });
    }

    handleCancel = (e) => {
        this.setState({
            disVisible: true,
            window:false,
        });
    }

    render() {
        const suffix = key ? <Icon type="close-circle" onClick={this.emitEmpty} /> : null;
        const {key} = this.state;

        return (
                <div>
                    <Modal
                        visible={this.state.window}
                        onOk={this.handleOk0}
                        onCancel={this.handleCancel}>
                        <img src={require('../../imgs/forbid.png' )}  style={{height:"100%",width:"100%",marginBottom:"10px"}}/>
                        <Input
                            placeholder="请输入通行码!"
                            prefix={<Icon type="question-circle-o" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            suffix={suffix}
                            value={key}
                            type="password"
                            onChange={this.onChangeUserName}
                            ref={node => this.keyInput = node}
                        />
                    </Modal>





                    {this.state.disVisible ? 'nothing' :
                        <div>
                        <Markdown source={"201905流水表\n" +
                        "\n" +
                        "|  value |  time | fund| credit/debt |remaining|zero|remark|\n" +
                        "| ------ | ------ | ------ |------ |------ |------ |------ |\n" +
                        "| 2019/05/31|工Z| 2.3  | credit||||\n" +
                        "| 2019/05/26|招Card | 0.2| debt |2.1| ||\n" +
                        "| 2019/06/09|花BEI | 0.5| debt|1.6|||\n" +
                        "| 2019/05/31|车WEI| 0.2| debt|1.4|||\n" +
                        "| 2019/06/05|广Card | 0.1| debt|1.3|||\n" +
                        "| 2019/06/15|铺地板 | 0.7| debt|0.6|||"}/>



                        <Markdown source={"201906流水表\n" +
                        "\n" +
                        "|  value |  time | fund| credit/debt |remaining|zero|remark|\n" +
                        "| ------ | ------ | ------ |------ |------ |------ |------ |\n" +
                        "| 2019/06/31|工Z| 2.3  | credit||||\n" +
                        "| 2019/06/26|招Card | 0.37| debt |1.93| ||\n" +
                        "| 2019/06/31|房Z | 0.53| debt|1.4|||\n" +
                        "| 2019/06/31|车WEI| 0.28| debt|1.12|||\n" +
                        "| 2019/07/05|广Card| 0.32| debt|0.80|||\n" +
                        "| 2019/07/09|花BEI| 0.05| debt|0.75|||"}/>


                        <Markdown source={"201907流水表\n" +
                        "\n" +
                        "|  value |  time | fund| credit/debt |remaining|zero|remark|\n" +
                        "| ------ | ------ | ------ |------ |------ |------ |------ |\n" +
                        "| 2019/07/31|工Z| 2.3  | credit||||\n" +
                        "| 2019/07/26|招Card | 0.37| debt |1.93| ||\n" +
                        "| 2019/07/31|车WEI+| 0.35| debt|1.58|||\n" +
                        "| 2019/08/05|广Card| 0.03| debt|1.55|||\n" +
                        "| 2019/08/09|花BEI| 0.05| debt|1.50|||"}/>
                        </div>
                    }
                </div>


        );
    }
}

export default JsPage;


